const images = {
    haze: require('./haze.jpg'),
    getImages: (name) => {
        const path = './' + name + '.jpg';
        console.log(path);
        try {
            return require('./' + name + '.jpg');
        }
        catch (err) {
            return require('./alternate.jpg');
        }
    }
}

export default images;


