import React, { Component } from 'react';
import { Input, Button, Card, Col, CardTitle } from 'react-materialize';
import images from './img/index';
//import cities from 'cities-list';

const openWeatherApiKey = '967a24720747c6bc8afdd4c9b8c667ad';
let cityInt = 'london';
// const url = 'https://api.openweathermap.org/data/2.5/weather?q=' + cityInt + '&APPID=' + openWeatherApiKey;


class Weather extends Component {
    constructor() {
        super();
        this.state = {
            city: '',
            temp: '',
            weather: '',
            humidity: '',
            isWeatherFound: false
        }
    }

    updateCityState = (e) => {
        //console.log(e.target.value);
        this.setState({
            city: e.target.value,
            isWeatherFound: false
        })
    }

    getWeather = async (cityForWeather) => {
        const url = 'https://api.openweathermap.org/data/2.5/weather?q=' + cityForWeather + '&APPID=' + openWeatherApiKey;
        await fetch(url)
            .then(results => results.json())
            .then(data => {
                console.log(data);
                console.log(data.weather[0].main);
                console.log(data.main.temp);
                this.setState({
                    temp: data.main.temp,
                    weather: data.weather[0].description,
                    humidity: data.main.humidity,
                    isWeatherFound: true
                })
            })
            .catch(err => {
                console.log('No data found');
                this.setState({
                    isWeatherFound: false
                })
            });
    }

    convertToFarenheit = (kelvinMeasurement) => {
        return (
            Math.round(((kelvinMeasurement * 9) / 5) - 459.67)
        )
    }

    convertToCelcius = (kelvinMeasurement) => {
        return (
            Math.round(kelvinMeasurement - 273.15)
        )
    }

    render() {
        return (
            <div>
                <Input placeholder="City" onChange={this.updateCityState} />
                <Button onClick={() => this.getWeather(this.state.city)}>Get Weather</Button>
                {this.state.isWeatherFound ?
                    <div className="row">
                        <Col m={4} s={12}>
                            <Card header={<CardTitle image={images.getImages(this.state.weather.toLowerCase())} alt={images.alternate}>{this.state.weather}</CardTitle>} className='black-text' title={this.state.city.toUpperCase()}>
                                <p>Overall Weather: {this.state.weather}</p>
                                <p className="white-text">Temperature: {this.convertToFarenheit(this.state.temp)}F / {this.convertToCelcius(this.state.temp)}C</p>
                                <p>Humidity: {this.state.humidity}%</p>
                            </Card>
                        </Col>
                    </div>
                    :
                    <div>
                    </div>
                }

            </div >
        )
    }
}

export default Weather;


